import { memo, useCallback, useContext, useEffect, useRef } from "react";
import { useNavigate } from 'react-router-dom';
import {
  Spinner,
  Wrap,
  WrapItem,
  Center,
  useDisclosure,
} from "@chakra-ui/react";

import { LoginUserContext } from '../../providers/LoginUserProvider';
import { useAllTournament } from "../../hooks/useAllTournament";
import {TournamentCard} from "../organisms/tournament/TournamentCard";

export const VotePage = memo(() => {

  const navigate = useNavigate();
  const { loginUser, setLoginUser } = useContext(LoginUserContext);
  const timerId = useRef<NodeJS.Timeout | null>(null);
  const { getTournaments, tournaments, loading } = useAllTournament();

  // 
  const autoLogoutTime = 600000; // 10
  //const autoLogoutTime = 10000; // 10

  useEffect(() => {
    const resetTimer = () => {
      // 
      if (timerId.current) {
        clearTimeout(timerId.current);
      }

      // 
      timerId.current = setTimeout(() => {
        // : setLoginUser(null)
        setLoginUser(null);

        // 
        navigate('/');
      }, autoLogoutTime);
    };

    // 
    const resetTimerOnAction = () => {
      resetTimer();
    };

    // 
    resetTimer();

    // : 
    document.addEventListener('click', resetTimerOnAction);

    return () => {
      // 
      if (timerId.current) {
        clearTimeout(timerId.current);
      }
      document.removeEventListener('click', resetTimerOnAction);
    };
  }, [setLoginUser, navigate, timerId]);

  // loginUser
  useEffect(() => {
    // loginUser
    if (!loginUser) {
      navigate('/');
    }
  }, [loginUser, navigate]);

  useEffect(() => getTournaments(), [getTournaments]);

  //const onClickTournament = useCallback();

  return (
    <>
     {loading ? (
        <Center h="100vh">
          <Spinner />
        </Center>
      ) : (
      <Wrap p={{ base: 4, md: 10 }}>
          {tournaments.map((tournament) => (
            <WrapItem key={tournament.id} mx="auto">
              <TournamentCard
                id={tournament.id}
                torName={tournament.name}
                date={tournament.date}
               // onClick={onClickTournament}
              />
            </WrapItem>
          ))}
        </Wrap>
      )}
    </>
  );
});
